@fill-tap : rgba((17,17,17,0.1));
.am-modal-wrap,
.am-modal-wrap.am-modal-wrap-popup {
  z-index: 999999;
}
.am-modal-mask {
  z-index: 9999;
}
.voucher-trend {
  background: #fff;
}
.voucher-trend.expand {
  background: transparent;
  margin: 0.3rem;
  height: auto;
  max-height: 100vh;
  overflow-y: auto;
}
.voucher-trend.expand .group:not(:last-child) {
  margin-bottom: 0.2rem;
}
.voucher-trend.expand .group:not(:last-child) .tail {
  height: 80%;
}
.voucher-trend.expand .group-content {
  margin-left: 0.2rem;
}
.voucher-trend .group {
  display: flex;
  align-items: center;
  margin-bottom: 0;
  margin-left: 0.2rem;
  position: relative;
}
.voucher-trend .group:not(:last-child) .tail {
  position: absolute;
  left: 0.2rem;
  top: 64%;
  height: 70%;
  border-left: 0.02rem dashed #D8D8D8;
}
.voucher-trend .group:first-child .icon {
  color: #E14C46;
}
.voucher-trend .group .icon {
  color: #cccccc;
}
.voucher-trend .group .icon.right-icon {
  color: #cccccc;
  position: absolute;
  right: -0.1rem;
  top: 50%;
  transform: translate(-50%, -50%);
}
.voucher-trend .group .group-content {
  width: 100%;
  background: #fff;
  padding: 0.2rem;
  border-radius: 0.08rem;
  line-height: 0.4rem;
  font-size: 0.24rem;
  color: #111;
}
.voucher-trend .group .group-content .bill {
  display: flex;
}
.voucher-trend .group .group-content .bill .name,
.voucher-trend .group .group-content .bill .phone {
  color: #1B8DE1;
  margin: 0rem 0.1rem;
}
.voucher-trend .group .group-content .bill .billno {
  display: flex;
}
.voucher-trend .group .group-content .bill .billno .split {
  margin-right: 0.1rem;
}
.voucher-trend .group .group-content .status {
  font-size: 0.28rem;
  font-weight: 900;
  display: flex;
}
.voucher-trend .group .group-content .status .details {
  display: flex;
}
.voucher-trend .group .group-content .status .split {
  margin-right: 0.1rem;
}
.voucher-trend .group .group-content .time {
  color: #888;
  font-size: 0.24rem;
  display: flex;
  align-items: center;
}
.voucher-trend .group .group-content .time .am-icon-xxs {
  width: 0.24rem;
  height: 0.24rem;
  margin: 0 0.1rem 0 0;
}
