@fill-tap : rgba((17,17,17,0.1));
.am-modal-wrap,
.am-modal-wrap.am-modal-wrap-popup {
  z-index: 999999;
}
.am-modal-mask {
  z-index: 9999;
}
.ems-card {
  margin: 0.3rem;
  border-radius: 0.15rem;
  background: white;
  color: #111111;
  font-size: 0.24rem;
  font-family: PingFangSC-Regular, PingFang SC;
}
.ems-card .number {
  display: flex;
  padding: 0.2rem 0.2rem 0.05rem;
  justify-content: space-between;
}
.ems-card .number .text {
  display: flex;
  align-items: center;
}
.ems-card .number .text .name-copy {
  font-size: 0.2rem;
  color: #555555;
  margin-left: 0.2rem;
  background: #f3f3f3;
  border-radius: 0.25rem;
  padding: 0.05rem 0.2rem;
}
.ems-card .number .icon {
  display: flex;
  align-items: center;
  color: #d0d0d0;
}
.ems-card .address {
  height: 1rem;
  font-size: 0.4rem;
  font-family: PingFang-SC-Bold, PingFang-SC;
  color: #111111;
  line-height: 0.4rem;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.05rem 0.2rem;
}
.ems-card .address .line {
  position: relative;
  width: 1.2rem;
  margin: 0 0.2rem;
  border-bottom: 0.02rem solid #888;
}
.ems-card .address .line .short-line {
  position: absolute;
  width: 0.2rem;
  border-bottom: 0.02rem solid #888;
  transform: rotate(45deg);
  right: -0.02rem;
  top: -0.08rem;
}
.ems-card .expname {
  padding: 0.05rem 0.2rem;
}
.ems-card .expname .code {
  color: #1A8DE1;
}
.ems-card .expect-time {
  padding: 0.05rem 0.2rem 0.2rem;
}
