@fill-tap : rgba((17,17,17,0.1));
.am-modal-wrap,
.am-modal-wrap.am-modal-wrap-popup {
  z-index: 999999;
}
.am-modal-mask {
  z-index: 9999;
}
.metaui-mobile-search {
  background-color: #fff;
  display: flex;
  position: relative;
  padding: 0.2rem 0.2rem 0.2rem 0;
}
.metaui-mobile-search-bar {
  width: calc(100% - 30 * 0.02rem);
  padding: 0 0.2rem;
}
.metaui-mobile-search-icons {
  width: 0.6rem;
  display: inline-flex;
  justify-content: space-evenly;
  align-items: center;
}
.metaui-mobile-search-icons .metaui-mobile-search-icon {
  display: flex;
  width: 0.48rem;
  height: 0.48rem;
}
.metaui-mobile-search::after {
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  height: 1PX;
  transform-origin: 50% 50%;
  transform: scaleY(0.5);
  background: #e9e9e9;
  bottom: 0;
  left: 0;
}
